<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data () {
    const validateAdmin = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入账户名'));
      } else {
        callback();
      }
    };
    const validatePass = (rule, value, callback) => {
      const reg = !value.trim().match(/^[a-zA-Z]{1,}\d{1,}$/) || value.trim().length < 6;

      if (reg) {
        callback(new Error('请输入以字母开头的不少于六位的数字字母组合密码'));
      } else {
        if (this.formCustom.passwdCheck !== '') {
            // 对第二个密码框单独验证
            this.$refs.formCustom.validateField('newpasswd2');
        }
        callback();
      }
    };
    const validatePassCheck = (rule, value, callback) => {
      if (value === '') {
          callback(new Error('请确认密码'));
      } else if (value !== this.formCustom.newpasswd1) {
          callback(new Error('两次密码输入不同!'));
      } else {
          callback();
      }
    };
    
    return {
      formCustom: {
        username: '',
        adminGroup: '',
        newpasswd1: '',
        newpasswd2: '',
        categoryList: ''
      },
      ruleCustom: {
        username: [
          { validator: validateAdmin, required: true, trigger: 'blur' }
        ],
        adminGroup: [
          { required: true, message:'请选择账户分组', trigger: 'change' }
        ],
        newpasswd1: [
          { required: true, validator: validatePass, trigger: 'blur' }
        ],
        newpasswd2: [
          { required: true, validator: validatePassCheck, trigger: 'blur' }
        ],
        categoryList: [
          { required: true, message:'请选择账户类别', trigger: 'change' }
        ],
      },
      data: {}
    }
  },
  mounted: function() {
    const token = getToken(),
          params = {"token": token};

    api.adminConfig(params).then(res => {
      console.log(res)
      if(res.code == 0) {
        this.data = res.data
        console.log(this.data)
      } else {
        this.$Notice.error({
          title: res.msg
        })
      }
    })
  },
  methods: {
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            const params = Object.assign({}, this.formCustom);
                  params.adminGroup*=1;
                  params.categoryList*=1;
                  params.token = getToken();

            console.log(this.formCustom, params)

            api.adminAdd(params).then(res => {
              if(res.code == 0) {
                this.$Notice.success({title:res.msg});
                this.$router.push({path: '/admin/adminlist'})
              } else {
                this.$Notice.error({desc:res.msg});
              }
            }).catch(err => {
              this.$Notice.error({desc:err});
            })
          } else {
            // this.$Notice.error({title:'Fail!'});
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields();
      }
  }
}
</script>
<template>
<div>
  <h3><Icon type="md-contract" size=24 />添加管理员</h3>
  <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="120">
    <FormItem label="账户" prop="username">
      <Input v-model="formCustom.username" placeholder="在此输入账户名"></Input>
    </FormItem>
    <FormItem label="账户分组" prop="adminGroup">
      <Select v-model="formCustom.adminGroup" placeholder="点击选择账户分组">
        <Option v-for="itm,index in data.groupList" :value="itm.id" :key="index">{{itm.title}}</Option>
      </Select>
    </FormItem>
    <FormItem label="密码" prop="newpasswd1">
      <Input type="password" v-model="formCustom.newpasswd1" placeholder="在此输入密码"></Input>
    </FormItem>
    <FormItem label="确认密码" prop="newpasswd2">
      <Input type="password" v-model="formCustom.newpasswd2" placeholder="在此输入确认密码"></Input>
    </FormItem>
    <FormItem label="账户类别" prop="categoryList">
      <Select v-model="formCustom.categoryList" placeholder="点击选择账户类别">
        <Option v-for="itm,index in data.categoryList" :value="index" :key="index">{{itm}}</Option>
      </Select>
    </FormItem>
    <FormItem>
        <Button type="primary" @click="handleSubmit('formCustom')">提交</Button>
        <Button @click="handleReset('formCustom')" style="margin-left: 8px">重置</Button>
    </FormItem>
  </Form>
</div>
</template>
<style lang="scss" scoped>
  .ivu-form {
		width: 28%;
	}
</style>